import { Component, HostBinding, Input, TemplateRef } from '@angular/core'
import { TreeNode, TreeUIOptions } from '../../models'

@Component({
    selector: 'ngx-tree-node-expander',
    template: `<ng-template [ngIf]="!template">
  <div *ngIf="node.hasChildren"
        class="toggle-children-wrapper"
        [class.toggle-children-expanded]="node.isExpanded"
        [class.toggle-children-collapsed]="node.isCollapsed"
        (click)="node.mouseAction('expanderClick', $event)">
    <span class="toggle-children"></span>
  </div>

  <div *ngIf="!node.hasChildren"
        class="toggle-children-placeholder"></div>
</ng-template>

<ng-container [ngTemplateOutlet]="template"
              [ngTemplateOutletContext]="{
                $implicit: null,
                node: node,
                index: index,
                options: options
              }">
</ng-container>
`,
    styles: [`:host{display:block;white-space:nowrap}.toggle-children-expanded .toggle-children{-webkit-transform:rotate(90deg);transform:rotate(90deg)}.toggle-children-collapsed .toggle-children{-webkit-transform:rotate(0);transform:rotate(0)}.toggle-children-wrapper{padding:0 3px;cursor:pointer}.toggle-children{background-image:url(data:image/png;base64,);height:8px;width:9px;background-size:contain;display:inline-block;background-repeat:no-repeat;background-position:center}.toggle-children-placeholder{display:block;height:15px;width:15px}`],
})
export class TreeNodeExpanderComponent {
    @Input() options: TreeUIOptions
    @Input() node: TreeNode
    @Input() index: number
    @Input() template: TemplateRef<any>

    @HostBinding('class.tree-node-expander') className = true
}
